<template>
  <component
      :class="$style.AppLink"
      :is="linkComponent"
      :[linkAttribute]="to || href"
  >
    <slot/>
  </component>
</template>

<script>
export default {
  name: "AppLink",
  computed: {
    linkAttribute() {
      return this.to !== null ? 'to' : 'href'
    },
    linkComponent() {
      return this.to !== null ? 'router-link' : 'a'
    }
  },
  props: {
    to: {type: String, default: null},
    href: {type: String, default: null},
  }
}
</script>

<style module>
.AppLink {
  color: #00ff87;
  text-decoration: none;
}

.AppLink:hover {
  color: #6fffbb;
}

.AppLink:active {
  color: #007940;
}
</style>