<template>
  <div :class="$style.PortfolioItem">
    <div :class="$style.PortfolioItemPreview">
      <img :src="getImageSrc(item.img)" :alt="item.title">
    </div>
    <div :class="$style.PortfolioItemDescription">
      <h1>{{ item.title }}</h1>
      <p>{{ item.description }}</p>
      <AppLink :href="`//${item.link}`">{{ item.link }}</AppLink>
    </div>
  </div>
</template>

<script>
import AppLink from "./AppLink";
export default {
  name: "PortfolioItem",
  methods: {
    getImageSrc(filename) {
      return require(`../assets/images/${filename}.png`)
    }
  },
  props: {
    item: { type: Object, required: true }
  },
  components: {AppLink}
}
</script>

<style module>
.PortfolioItem {
  display: flex;
  gap: 1rem;
  overflow: hidden;
  border-radius: 5px;
}

.PortfolioItemPreview {
  width: 33.333333%;
}
.PortfolioItemDescription {
  width: 66.666667%;
}

.PortfolioItemPreview {
  display: flex;
}

.PortfolioItemPreview img {
  max-width: 100%;
}
</style>